import { ApolloClient, InMemoryCache, createHttpLink, from } from '@apollo/client';
import { setContext } from '@apollo/client/link/context';
import { onError } from '@apollo/client/link/error';
import toast from 'react-hot-toast';

// URL do GraphQL do Magento (ajuste conforme sua configurao)
const GRAPHQL_ENDPOINT = process.env.REACT_APP_MAGENTO_GRAPHQL_URL || 'http://srv791323.hstgr.cloud/graphql';

// Link HTTP
const httpLink = createHttpLink({
  uri: GRAPHQL_ENDPOINT,
  credentials: 'include', // Incluir cookies se necessrio
  fetchOptions: {
    mode: 'cors', // Forar modo CORS
  },
});

// Link de autenticao
const authLink = setContext((_, { headers }: any) => {
  // Obter token do localStorage
  const token = localStorage.getItem('magento_customer_token');
  
  return {
    headers: {
      ...headers,
      authorization: token ? `Bearer ${token}` : '',
      'Content-Type': 'application/json',
    }
  };
});

// Link de tratamento de erros
const errorLink = onError(({ graphQLErrors, networkError, operation, forward }: any) => {
  console.log('GraphQL Error Details:', { graphQLErrors, networkError, operation });
  
  if (graphQLErrors) {
    graphQLErrors.forEach(({ message, locations, path }: any) => {
      console.error(
        `[GraphQL error]: Message: ${message}, Location: ${locations}, Path: ${path}`
      );
      
      // No mostrar toast para mutations - elas so tratadas pelos hooks especficos
      const isMutation = operation?.query?.definitions?.[0]?.operation === 'mutation';
      
      // S mostrar toast para queries, no para mutations
      if (!isMutation) {
        toast.error(`Erro GraphQL: ${message}`);
      }
    });
  }

  if (networkError) {
    console.error(`[Network error]:`, networkError);
    
    // Verificar se  erro de CORS
    if (networkError.message?.includes('CORS') || networkError.message?.includes('cors')) {
      toast.error('Erro de CORS. Verifique a configurao do servidor.');
    }
    // Verificar se  erro de conexo
    else if (networkError.message?.includes('Failed to fetch') || networkError.message?.includes('NetworkError')) {
      toast.error('Erro de conexo. Verifique se o servidor est rodando.');
    }
    // Verificar se  erro de autenticao
    else if ((networkError as any).statusCode === 401) {
      // Temporariamente desabilitado o redirecionamento automtico
      // para debug do problema de redirecionamento na criao de conta
      console.log('401 Error - not redirecting for debug');
      toast.error('Erro de autenticao. Verifique suas credenciais.');
    } else {
      toast.error(`Erro de conexo: ${networkError.message || 'Tente novamente.'}`);
    }
  }
});

// Criar cliente Apollo
const client = new ApolloClient({
  link: from([errorLink, authLink, httpLink]),
  cache: new InMemoryCache({
    typePolicies: {
      Customer: {
        fields: {
          addresses: {
            merge(existing: any = [], incoming: any) {
              return incoming;
            }
          }
        }
      }
    }
  }),
  defaultOptions: {
    watchQuery: {
      errorPolicy: 'all',
    },
    query: {
      errorPolicy: 'all',
    },
    mutate: {
      errorPolicy: 'all',
    },
  },
});

export default client;
